import React, { useEffect, useState } from "react";
import "./grid.css";
import {
  faPlay,
  faRedo,
  faSave
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Editor from "./Editor";
import OutputScreen from "./Output_Screen";
import configData from "../config/config.json";
import PasteClient from "pastebin-api";
import { Modal } from "@material-ui/core";
import Sharelink from "./share_link";


const Grid = () => {
  const [html, setHtml] = useState("");
  const [css, setCss] = useState("");
  const [js, setJs] = useState("");
  const [saved_url, setSavedUrl] = useState("Loading");

  const [srcDoc, setSrcDoc] = useState("");

  const [page, setPage] = useState("HTML");
  const [isShare,setShare]=useState(false);
  function reload(){
    setSrcDoc(`
        <html>
          <body>${html}</body>
          <style>${css}</style>
          <script>${js}</script>
        </html>
      `);
  }

  async function save_code() {
    const code={
      "html":html,
      "js":js,
      "css":css
    }
    try{
      setShare(true);
      const client = new PasteClient(configData.api_dev_key);
      const token = await client.login(configData.username,configData.password);
      console.log(token);
      const url = await client.createPaste({
        code: code,
        expireDate: configData.api_paste_expire_date,
        format: configData.api_paste_format,
        name: "test.js",
        publicity: 0,
        apiUserKey:token
      });
      console.log(url)
      setSavedUrl(url);
      console.log(saved_url);
  }
  catch(error){
    setSavedUrl("Request has been blocked by CORS policy of the PasteBin Server")
  }
  }

  useEffect(() => {
    const timeout = setTimeout(() => {
      setSrcDoc(`
        <html>
          <body>${html}</body>
          <style>${css}</style>
          <script>${js}</script>
        </html>
      `);
    }, 500);

    return () => clearTimeout(timeout);
  }, [html, css, js]);

  return (
    <div className="grid">
      <div className="col ">
        <div className="col nav">
          <div className="col nav-left">
            <center>
            <i  className="col"><img style={{objectFit:"contain"}} alt="Dyte" src="https://www.dyte.io/images/Dyte-Logo.svg"></img></i>
            </center>
          </div>
        </div>
        
        
                                                              {/* Files Pannel */}
                                                              
        <div className="col files">
          <div className="col">
            <h4>Online Html Editor</h4>
          </div>
          
          <div className="col " onClick={() => {setPage("HTML");}}>
            <h4>HTML</h4> 
            <p>index.html</p>
          </div>
          <div className="col " onClick={() => {setPage("JS");}}>
            <h4 >JS</h4>
            <p >index.js</p>
          </div>
          <div className="col " onClick={() => {setPage("CSS");}}>
            <h4 >CSS</h4>
            <p >index.css</p>
          </div>
        </div>
      </div>
                                                          {/* Editor Pannel */}
      <div className="col ">
        <div className="col nav" style={{ backgroundColor: "#151515" }}>
          <div className="col nav-left">
            <div className="editor-title" onClick={() => {setPage("HTML")}}>
              index.html
              <button >
                <FontAwesomeIcon onClick={()=>setHtml('')} className="col" icon={faRedo} />
              </button>
            </div>
            <div className="editor-title" onClick={() => {setPage("JS")}}>
            index.js
              <button  >
                <FontAwesomeIcon onClick={()=>setJs('')}  className="col" icon={faRedo} />
              </button>
            </div>
            <div className="editor-title" onClick={() => {setPage("CSS")}}>
            index.css
              <button >
                <FontAwesomeIcon className="col" onClick={()=>setCss('')} icon={faRedo} />
              </button>
            </div>
          </div>
        </div>
        <div className="col code ">
          <div className="editor-container">
            <small>
          {page === "HTML" ? (
            <Editor
              title="index.html"
              language="xml"
              value={html}
              onChange={setHtml}
            />
          ) : (
            <></>
          )}
          {page === "CSS" ? (
            <Editor
              title="index.css"
              language="css"
              value={css}
              onChange={setCss}
            />
          ) : (
            <></>
          )}
          {page === "JS" ? (
            <Editor
              title="index.js"
              language="javascript"
              value={js}
              onChange={setJs}
            />
          ) : (
            <></>
          )}
            </small>
          </div>
        </div>
      </div>

                                                    {/* Output Pannel */}

      <div className="col ">
        <div className="col nav">
          <div className="col nav-left" style={{color:"lightgreen"}}>Live View</div>
          <div className="col nav-right">
            <FontAwesomeIcon onClick={()=>{save_code()}} className="col" icon={faSave} />   {/* Save the code in Paste Bin */}
            <FontAwesomeIcon onClick={()=>{reload()}} className="col"  icon={faPlay} />       {/* optional Manul Relaod the Button */}

          </div>
        </div>
        <div className="col output">
        <OutputScreen  srcDoc={srcDoc} />
          <Modal open={isShare} onClose={() => setShare(false)}>
          <Sharelink url={saved_url} />
          </Modal>
        </div>
      </div>
    </div>
  );
};

export default Grid;
